.t-xl-r {
  font-size: 24px;
}
.t-l-r {
  font-size: 20px;
}
.t-s-r {
  font-size: 12px;
}
.header-color {
  color: var(--header-color);
}
.clickable {
  cursor: pointer;
}
.d-flx {
  display: flex;
}
.flx-column {
  flex-direction: column;
}
.flx-center {
  justify-content: center;
  align-items: center;
}
.flx-1 {
  flex-grow: 1;
}
.flx-row {
  flex-direction: row;
}
.gap-s {
  gap: 8px;
}
.gap-l {
  gap: 16px;
}
.m-b-s {
  margin-bottom: 8px;
}
.m-t-l {
  margin-top: 16px;
}
.m-b-l {
  margin-bottom: 16px;
}
:root {
  --main-color: #90E0EF;
  --text-color: black;
  --background-color: white;
  --content-backgrount-color: #03045E;
  --text-description-color: #0077B6;
  --background-active: #00BBE0;
}
.dark-theme {
  --main-color: #1a1a1a;
  --text-color: white;
  --content-backgrount-color: white;
  --background-color: #1a1a1a;
  --text-description-color: gray;
  --background-active: white;
}
.mat-mdc-table {
  background-color: var(--background-color) !important;
}
.mat-mdc-header-cell {
  color: var(--text-color) !important;
  align-content: center;
  border-bottom: none !important;
}
.mat-mdc-row {
  color: var(--text-color) !important;
}
.mat-mdc-row .mat-mdc-cell {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  cursor: pointer;
}
